	module.exports = {"data":{"site":{"siteMetadata":{"title":"Annalisa Valente"}},"contentfulPageSelectorPanel":{"pages":[{"name":"Brave the Shave","title":null,"slug":"/projects/brave-the-shave/","description":"Redesigning Brave the Shave microsite"},{"name":"Mindfulness Moments","title":"Mindfulness Moments","slug":"/projects/mindfulness-moments/","description":"Building a new fundraising proposition"},{"name":"Local Cancer Intelligence","title":"Local cancers intelligence","slug":"/projects/local-cancer-intelligence/","description":"New filter system for the LCI website"},{"name":"Google","title":"Google","slug":"/projects/google/","description":"Interaction designer contractor in the Google Play Console team"},{"name":"The Telegraph","title":"The Telegraph","slug":"/projects/the-telegraph/","description":"Working on various digital newspaper touch points"},{"name":"The Telegraph puzzle ","title":null,"slug":"/projects/the-telegraph-puzzle/","description":"Redesign the experience of the Telegraph puzzle website"},{"name":"Nectar ","title":"Nectar","slug":"/projects/nectar/","description":"  New way of discovering Nectar brands for web and iOS-Android app"},{"name":"MyMail & Nectar","title":"MyMail & Nectar","slug":"/projects/mymail-and-nectar/","description":"Launch of MyMail and Nectar proposition"},{"name":"Remember names","title":null,"slug":"/projects/face-name/","description":"Concept of an Android experience that helps to match faces to names"}]},"contentfulPage":{"slug":"/about/","password":null,"panels":[{"__typename":"ContentfulImagePanel","fullWidth":false,"media":{"description":"Testing description","file":{"url":"//images.ctfassets.net/wk0hltq7eofw/6dUxyw49e8mOWoOQowguMs/bb1f85747295616bed8d84378915a2fc/g__1_.jpg"}}},{"__typename":"ContentfulContentPanel","content":{"content":"__Key points of my design experience:__\n\n- Design thinking approach, always user first\n- Web and Mobile experience\n- Working closely with stakeholders, eng, PO, and marketers, helping them to ship products and to prioritise features\n- Fast mocking, prototyping and testing approach in order to collect feedback at the early stage of the process and iterate\n- Data driven \n- Believing that communication and decision making are the key to make a great team and help team members to build great products\n\nI like to work on complex design challenges helping companies to launch products and services. Working closely with stakeholders and developers I help the teams to identify constrains at the very beginning. I'm able to organise design sprints and workshops in order to help identify design opportunities.\n\n\n\n__I share the vision of good design with <a href=\"https://www.interaction-design.org/literature/article/dieter-rams-10-timeless-commandments-for-good-design\" target=\"_blank\" title=\"External link to Interaction Design\">Dieter Rams</a>:__\n\n1) Good design is innovative\n2) Good design makes a product useful\n3) Good design is aesthetic\n4) Good design makes a product understandable\n5) Good design is unobtrusive\n6) Good design is honest\n7) Good design is long-lasting\n8) Good design is thorough down to the last detail\n9) Good design is environmentally friendly\n10) Good design involves as little design as possible\n\nAdding:\n11) Good design is accessible\n\n\n__My interests:__\n\nOutside the office I am busy reading, studying and working on projects regarding design and development. I am very interested in ethics of information and design, accessibility and inclusive design. \n\n\nI am a member of the Jury at <a href=\"https://www.cssdesignawards.com/\" target=\"_blank\" title=\"External link to CSS Design Awards\">www.cssdesignawards.com</a> and sometimes I like to write articles and to participate at hackathons. I'm also helping at the <a href=\"https://uxpa-uk.org/\" target=\"_blank\" title=\"External link to UXPA UK\">UXPA UK</a>.\n\nIn my free time I like playing D&D, board games and learning drums.","childMarkdownRemark":{"html":"<p><strong>Key points of my design experience:</strong></p>\n<ul>\n<li>Design thinking approach, always user first</li>\n<li>Web and Mobile experience</li>\n<li>Working closely with stakeholders, eng, PO, and marketers, helping them to ship products and to prioritise features</li>\n<li>Fast mocking, prototyping and testing approach in order to collect feedback at the early stage of the process and iterate</li>\n<li>Data driven </li>\n<li>Believing that communication and decision making are the key to make a great team and help team members to build great products</li>\n</ul>\n<p>I like to work on complex design challenges helping companies to launch products and services. Working closely with stakeholders and developers I help the teams to identify constrains at the very beginning. I'm able to organise design sprints and workshops in order to help identify design opportunities.</p>\n<p><strong>I share the vision of good design with <a href=\"https://www.interaction-design.org/literature/article/dieter-rams-10-timeless-commandments-for-good-design\" target=\"_blank\" title=\"External link to Interaction Design\">Dieter Rams</a>:</strong></p>\n<ol>\n<li>Good design is innovative</li>\n<li>Good design makes a product useful</li>\n<li>Good design is aesthetic</li>\n<li>Good design makes a product understandable</li>\n<li>Good design is unobtrusive</li>\n<li>Good design is honest</li>\n<li>Good design is long-lasting</li>\n<li>Good design is thorough down to the last detail</li>\n<li>Good design is environmentally friendly</li>\n<li>Good design involves as little design as possible</li>\n</ol>\n<p>Adding:</p>\n<ol start=\"11\">\n<li>Good design is accessible</li>\n</ol>\n<p><strong>My interests:</strong></p>\n<p>Outside the office I am busy reading, studying and working on projects regarding design and development. I am very interested in ethics of information and design, accessibility and inclusive design. </p>\n<p>I am a member of the Jury at <a href=\"https://www.cssdesignawards.com/\" target=\"_blank\" title=\"External link to CSS Design Awards\">www.cssdesignawards.com</a> and sometimes I like to write articles and to participate at hackathons. I'm also helping at the <a href=\"https://uxpa-uk.org/\" target=\"_blank\" title=\"External link to UXPA UK\">UXPA UK</a>.</p>\n<p>In my free time I like playing D&#x26;D, board games and learning drums.</p>"}}}]}},"pathContext":{"slug":"/about/","p":"35675e68f4b5af7b995d9205ad0fc43842f16450"}}

